import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute , Router } from '@angular/router';
import { CountdownComponent, CountdownConfig } from 'ngx-countdown';
import { CountDownTime } from 'src/app/Interfaces/countDownTime';
import {competitionTime} from './../../countDownDate'

@Component({
  selector: 'app-count-down',
  templateUrl: './count-down.component.html',
  styleUrls: ['./count-down.component.scss']
})
export class CountDownComponent implements OnInit {
  @ViewChild("countdown", { static: false })
  competitionStartTime: CountDownTime; 
  configTime: CountdownConfig;

  timeTextImage: string = "https://ihawk.s3.us-east-2.amazonaws.com/Coming-Soon-02.png";

  constructor(
    private _route: Router,
    private _activatedRoute: ActivatedRoute 
    ) { }
  
  ngOnInit() {


    let filtrationDate = new Date(competitionTime.challengeTime)
    let currentTime = new Date(Date.now());

    let timeInSec = filtrationDate.getTime() / 1000 - currentTime.getTime() / 1000;
    let daysLeft = Math.floor(timeInSec / (60*60*24))
    let day = 86400  //60x60x24

    if (timeInSec >= day * 10)
    {

      this.setTime(`${daysLeft}:HH:mm:ss`, timeInSec);
    }
   else if (timeInSec < day*10 && timeInSec > day) this.setTime(`0${daysLeft}:HH:mm:ss`, timeInSec)
   else if (timeInSec < day && timeInSec >= 3600) this.setTime(`00:HH:mm:ss`, timeInSec) 
   else if (
    timeInSec < day &&
    timeInSec < 3600 &&
    timeInSec >= 0
  ) this.setTime(`00:00:mm:ss`, timeInSec)
  // GAME STARTED
   else if (timeInSec <= 0 && timeInSec > day * -1) {
      this.timeTextImage = "https://ihawk.s3.us-east-2.amazonaws.com/Game+Started.png";
      timeInSec = (filtrationDate.getTime() + 86400000) / 1000 - currentTime.getTime() / 1000;
      let hours = Math.floor(timeInSec / 3600);
      if (hours > 10) this.setTime(`00:${hours}:mm:ss`, timeInSec)
      else this.setTime(`00:0${hours}:mm:ss`, timeInSec);
   }
     
   else {
   this.timeTextImage = "https://ihawk.s3.us-east-2.amazonaws.com/Game+Finished.png";
   this.setTime(`00:00:00:00`, 0);
   
   }
  }



  setTime(format, timeInSec) {
    this.configTime = {
      leftTime: timeInSec,
      format: format,
      timezone: "+0000",
      prettyText: (text) => {
        return text
          .split(":")
          .map((v, index) => `<span class="item${index}">${v}</span>`)
          .join(":");
      },
    };
  }
}
